import * as React from "react";
import * as Yup from "yup";
import { useFormik } from "formik";

interface AdministratorProps {
  nextStep?: any;
  handleChange: Function;
  values?: any;
}

const Administrator: React.FC = (
  props: React.PropsWithChildren<AdministratorProps>
) => {
  const [loading, setLoading] = React.useState(false);

  const { nextStep, handleChange, values } = props;

  const { first_name, last_name, email_address, phone_number, password } =
    values;

  const enableLoading = () => {
    setLoading(true);
  };

  const disableLoading = () => {
    setLoading(false);
  };

  const phoneRegExp =
    /\+(9[976]\d|8[987530]\d|6[987]\d|5[90]\d|42\d|3[875]\d|2[98654321]\d|9[8543210]|8[6421]|6[6543210]|5[87654321]|4[987654310]|3[9643210]|2[70]|7|1)\d{1,14}$/;

  const initialValues = {
    first_name: "",
    last_name: "",
    tel: "",
    email: "",
    password: "",
  };

  const RegistrationSchema = Yup.object().shape({
    first_name: Yup.string()
      .min(3, "Minimum 3 symbols")
      .max(50, "Maximum 50 symbols")
      .required("AUTH.VALIDATION.REQUIRED_FIELD"),
    last_name: Yup.string()
      .min(3, "Minimum 3 symbols")
      .max(50, "Maximum 50 symbols")
      .required("AUTH.VALIDATION.REQUIRED_FIELD"),
    // tel: Yup.string().matches(phoneRegExp, 'AUTH.VALIDATION.REQUIRED_FIELD'),
    email: Yup.string()
      .email("Wrong email format")
      .min(3, "Minimum 3 symbols")
      .max(50, "Maximum 50 symbols")
      .required("AUTH.VALIDATION.REQUIRED_FIELD"),
    password: Yup.string()
      .min(3, "Minimum 3 symbols")
      .max(50, "Maximum 50 symbols")
      .required("AUTH.VALIDATION.REQUIRED_FIELD"),
  });

  const formik = useFormik({
    initialValues,
    validationSchema: RegistrationSchema,
    onSubmit: (values, { setSubmitting }) => {
      const body = {};
      body["first_name"] = values.first_name;
      //   handleChange(body);

      enableLoading();
      nextStep();
      setSubmitting(false);
    },
  });

  const Continue = (e: any) => {
    e.preventDefault();
    formik.handleSubmit();
    // nextStep()
  };

  return (
    <>
      <div className='md:col-span-1 mb-5'>
        <div className='px-4 sm:px-0'>
          <h3 className='text-lg font-medium leading-6 text-gray-900'>
            Personal Information
          </h3>
          <p className='mt-1 text-sm text-gray-600'>
            Use a permanent address where you can receive mail.
          </p>
        </div>
      </div>
      <div className='mt-5 md:col-span-2 md:mt-0'>
        <form action='#' method='POST'>
          <div className='overflow-hidden shadow sm:rounded-md'>
            <div className='bg-white px-4 py-5 sm:p-6'>
              {/* begin: Alert */}
              {formik.status && (
                // <div className="mb-10 alert alert-custom alert-light-danger alert-dismissible">
                //   <div className="alert-text font-weight-bold">
                <p className='text-sm font-medium text-gray-700'>
                  {formik.status}
                </p>
                //   </div>
                // </div>
              )}
              {/* end: Alert */}
              <div className='grid grid-cols-6 gap-6'>
                {/* BEGIN: First Name */}
                <div className='col-span-6 sm:col-span-3'>
                  <label
                    htmlFor='first-name'
                    className='block text-sm font-medium text-gray-700'
                  >
                    First name
                  </label>
                  <input
                    // onChange={handleChange('first_name')}
                    // value={first_name}
                    {...formik.getFieldProps("first_name")}
                    type='text'
                    name='first_name'
                    id='first-name'
                    autoComplete='given-name'
                    className='mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm'
                  />
                  {formik.touched.first_name && formik.errors.first_name ? (
                    <div className='fv-plugins-message-container'>
                      <div className='fv-help-block'>
                        {formik.errors.first_name}
                      </div>
                    </div>
                  ) : null}
                </div>
                {/* END: First Name */}

                {/* BEGIN: Last name */}
                <div className='col-span-6 sm:col-span-3'>
                  <label
                    htmlFor='last-name'
                    className='block text-sm font-medium text-gray-700'
                  >
                    Last name
                  </label>
                  <input
                    value={values?.last_name}
                    onChange={handleChange("last_name")}
                    type='text'
                    name='last-name'
                    id='last-name'
                    autoComplete='family-name'
                    className='mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm'
                  />
                  {formik.touched.last_name && formik.errors.last_name ? (
                    <div className='fv-plugins-message-container'>
                      <div className='fv-help-block'>
                        {formik.errors.last_name}
                      </div>
                    </div>
                  ) : null}
                </div>
                {/* END: Last name */}

                {/* begin: Email */}
                <div className='col-span-6 sm:col-span-3'>
                  <label
                    htmlFor='first-name'
                    className='block text-sm font-medium text-gray-700'
                  >
                    Email Address
                  </label>
                  <input
                    value={values?.email_address}
                    onChange={handleChange("email_address")}
                    type='email'
                    name='email-address'
                    id='email-address'
                    autoComplete='email'
                    className='mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm'
                  />
                  {formik.touched.email && formik.errors.email ? (
                    <div className='fv-plugins-message-container'>
                      <div className='fv-help-block'>{formik.errors.email}</div>
                    </div>
                  ) : null}
                </div>
                {/* end: Email */}

                {/* BEGIN: Phone Number */}
                <div className='col-span-6 sm:col-span-3'>
                  <label
                    htmlFor='phone-number'
                    className='block text-sm font-medium text-gray-700'
                  >
                    Phone Number
                  </label>
                  <input
                    value={values?.phone_number}
                    onChange={handleChange("phone_number")}
                    type='text'
                    name='phone-number'
                    id='phone-number'
                    autoComplete='phone-number'
                    className='mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm'
                  />
                  {formik.touched.tel && formik.errors.tel ? (
                    <div className='fv-plugins-message-container'>
                      <div className='fv-help-block'>{formik.errors.tel}</div>
                    </div>
                  ) : null}
                </div>
                {/* END: Phone Number */}

                {/* BEGIN: License Number */}
                <div className='col-span-6 sm:col-span-6'>
                  <label
                    htmlFor='license-number'
                    className='block text-sm font-medium text-gray-700'
                  >
                    License Number
                  </label>
                  <input
                    value={values?.license_number}
                    onChange={handleChange("license_number")}
                    type='text'
                    name='license-number'
                    id='license-number'
                    className='mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm'
                  />
                </div>
                {/* END: License Number */}

                {/* BEGIN: Password */}
                <div className='col-span-6 sm:col-span-3'>
                  <label
                    htmlFor='password'
                    className='block text-sm font-medium text-gray-700'
                  >
                    Password
                  </label>
                  <input
                    value={values?.password}
                    onChange={handleChange("password")}
                    type='password'
                    name='password'
                    id='password'
                    autoComplete='given-name'
                    className='mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm'
                  />
                  {formik.touched.password && formik.errors.password ? (
                    <div className='fv-plugins-message-container'>
                      <div className='fv-help-block'>
                        {formik.errors.password}
                      </div>
                    </div>
                  ) : null}
                </div>
                {/* END: Password */}
                <div className='col-span-6 sm:col-span-3'>
                  <label
                    htmlFor='confirm-password'
                    className='block text-sm font-medium text-gray-700'
                  >
                    Confirm Password
                  </label>
                  <input
                    value={values?.confirm_password}
                    onChange={handleChange("confirm_password")}
                    type='password'
                    name='confirm-password'
                    id='confirm-password'
                    autoComplete='family-name'
                    className='mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm'
                  />
                </div>
              </div>
            </div>

            <div className='bg-gray-50 px-4 py-3 text-right sm:px-6'>
              <button
                type='submit'
                disabled={formik.isSubmitting}
                onClick={Continue}
                className='inline-flex justify-center rounded-md border border-transparent bg-indigo-600 py-2 px-4 text-sm font-medium text-white shadow-sm hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2'
              >
                <span>Next</span>
                {loading && (
                  <div
                    className='spinner-border animate-spin inline-block w-8 h-8 border-4 rounded-full'
                    role='status'
                  >
                    <span className='visually-hidden'>Loading...</span>
                  </div>
                  //   <svg
                  //     aria-hidden="true"
                  //     className="w-8 h-8 mr-2 text-gray-200 animate-spin dark:text-gray-600 fill-blue-600"
                  //     viewBox="0 0 100 101"
                  //     fill="none"
                  //     xmlns="http://www.w3.org/2000/svg"
                  //   >
                  //     <path
                  //       d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z"
                  //       fill="currentColor"
                  //     />
                  //     <path
                  //       d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z"
                  //       fill="currentFill"
                  //     />
                  //   </svg>
                  // <span class="sr-only">Loading...</span>
                )}
              </button>
            </div>
          </div>
        </form>
      </div>
    </>
  );
};

export default Administrator;
